"use client";

import { useEffect, useRef, useState } from "react";

type BiographyProps = {
  text: string;
  collapsedLines?: number;
};

export default function Biography({
  text,
  collapsedLines = 5,
}: BiographyProps) {
  const [expanded, setExpanded] = useState(false);
  const [isOverflowing, setIsOverflowing] = useState(false);
  const contentRef = useRef<HTMLParagraphElement>(null);

  useEffect(() => {
    if (!contentRef.current) return;

    const el = contentRef.current;
    setIsOverflowing(el.scrollHeight > el.clientHeight);
  }, [text]);

  if (!text || text.trim() === "") {
    return (
      <p className="text-sm italic text-gray-400">No biography available.</p>
    );
  }

  return (
    <div>
      <h3 className="font-bold text-xl mb-2">Biography</h3>

      <div
        className={`relative overflow-hidden transition-all duration-500 ease-in-out ${
          expanded ? "max-h-[2000px]" : "max-h-[8.5rem]"
        }`}
      >
        <p
          ref={contentRef}
          className={`text-sm leading-relaxed ${
            expanded ? "" : `line-clamp-${collapsedLines}`
          }`}
        >
          {text}
        </p>

        {/* Fade gradient when collapsed */}
        {!expanded && isOverflowing && (
          <div className="absolute bottom-0 left-0 h-10 w-full bg-gradient-to-t from-black/90 to-transparent pointer-events-none" />
        )}
      </div>

      {isOverflowing && (
        <button
          onClick={() => setExpanded((prev) => !prev)}
          aria-expanded={expanded}
          className="mt-2 text-sm font-semibold text-blue-500 hover:underline focus:outline-none focus:ring-2 focus:ring-blue-400 rounded"
        >
          {expanded ? "Read less" : "Read more"}
        </button>
      )}
    </div>
  );
}
